import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import * as CryptoJS from 'crypto-js';

@Injectable({
  providedIn: 'root'
})
export class DevPickerService {
  public rawData = {};
  public pickedDev;
  public pickedSprint;
  public pickedSprintName: string;
  public devsWithStuffInTheSprint = [];
  public decrypted = false;
  devWhiteList = [
    'Ivan Zinkevich',
    'Javier Elizalde Solis',
    'Jhonatan Caceres Acevedo',
    'Jonathan H. Fern√°ndez',
    'Juan Francisco Bielma Vargas',
    'Luis Lopez',
    'Roberto Roman',
    'Sergio Torres Santander',
    'Slava Gruzdov'
  ]
  public updateData$ = new Subject<void>()

  constructor(private httpService: HttpClient) {
    
  }

  decryptData(password: string) {
    this.httpService.get('/assets/raw-data.json',{responseType: 'text'}).subscribe(rawData => {
      const data = CryptoJS.AES.decrypt(rawData, password.trim()).toString(CryptoJS.enc.Utf8);
      this.rawData = JSON.parse(data);
      const firstSprint = Object.keys(this.rawData)[0]
      this.pickSprint(firstSprint);
      this.decrypted = true;
    });
  }

  pickDev(developer: any) {
    this.pickedDev = this.pickedSprint.find(dev => dev.developer === developer)
    this.updateData$.next()
  }

  pickSprint(sprint: string) {
    this.pickedSprint = this.rawData[sprint].filter(dev => this.devWhiteList.includes(dev.developer))
    this.devsWithStuffInTheSprint = this.pickedSprint.map(dev => dev.developer)
    this.pickedSprintName = sprint;
    if (this.pickedDev && this.pickedSprint.find(dev => dev.developer === this.pickedDev.developer)) {
      this.pickDev(this.pickedDev.developer)
    } else {
      this.pickSUmmary()
    }
    this.updateData$.next()
  }

  pickSUmmary() {
    this.pickedDev = this.getSummary()
    this.updateData$.next()
  }

  getSummary(pickedSprint?) {
    if (!pickedSprint) {
      pickedSprint = this.pickedSprint;
    } else {
      pickedSprint = this.rawData[pickedSprint].filter(dev => this.devWhiteList.includes(dev.developer))
    }
    const summary = {} as any
    Object.keys(pickedSprint[0]).forEach(property => {
      if (typeof pickedSprint[0][property] === 'number') {
        summary[property] = pickedSprint.reduce((acc, act) => acc + act[property], 0);
      } else if (typeof pickedSprint[0][property] === 'object' && typeof pickedSprint[0][property].length === 'number') {
        summary[property] = pickedSprint.reduce((acc, act) => [...acc,...act[property]], []);
      }  else if (typeof pickedSprint[0][property] === 'string') {
        summary[property] = 'Sprint Summary'
      }
    });
    return summary;
  }
}
