import { map } from 'rxjs/operators';
import { Breakpoints, BreakpointObserver } from '@angular/cdk/layout';
import { DevPickerService } from '../dev-picker.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { ChartDataSets, ChartOptions } from 'chart.js';
import { Color, BaseChartDirective, Label } from 'ng2-charts';

@Component({
  selector: 'app-summary-line-chart',
  templateUrl: './summary-line-chart.component.html',
  styleUrls: ['./summary-line-chart.component.scss']
})
export class SummaryLineChartComponent implements OnInit {
  public lineChartData: ChartDataSets[] = [];
  public lineChartLabels: Label[] = [];
  public lineChartOptions: (ChartOptions & { annotation: any }) = {
    responsive: true,
    scales: {
      // We use this empty structure as a placeholder for dynamic theming.
      xAxes: [{}],
      yAxes: [
        {
          id: 'y-axis-0',
          position: 'left',
        }
      ]
    },
    annotation: {
      annotations: [
        {
          type: 'line',
          mode: 'vertical',
          scaleID: 'x-axis-0',
          value: 'March',
          borderColor: 'orange',
          borderWidth: 2,
          label: {
            enabled: true,
            fontColor: 'orange',
            content: 'LineAnno'
          }
        },
      ],
    },
  };
  public lineChartColors: Color[] = [
    /*{ // grey
      backgroundColor: 'rgba(63,127,191,0.2)',
      borderColor: 'rgba(63,127,191,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    },*/
    { // dark grey
      backgroundColor: 'rgba(63,191,63,0.2)',
      borderColor: 'rgba(63,191,63,1)',
      pointBackgroundColor: 'rgba(77,83,96,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(77,83,96,1)'
    },
    { // red
      backgroundColor: 'rgba(191,127,63,0.3)',
      borderColor: 'rgba(191,127,63,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    },
    { // red
      backgroundColor: 'rgba(191,63,63,0)',
      borderColor: 'rgba(191,63,63,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    }
  ];
  public lineChartLegend = true;
  public lineChartType = 'line';

  @ViewChild(BaseChartDirective, { static: true }) chart: BaseChartDirective;

  constructor(private breakpointObserver: BreakpointObserver, public picker: DevPickerService) {}

  ngOnInit() {
    this.picker.updateData$.subscribe(() => {
      this.lineChartLabels = Object.keys(this.picker.rawData).sort();
      this.lineChartData = [
        /*{ data: this.lineChartLabels.map(sprint => {
          if (this.picker.pickedDev.developer === 'Sprint Summary') {
            return this.picker.getSummary(sprint).totalStoryPoints;
          } else {
            const index = this.picker.rawData[`${sprint}`].findIndex(d => d.developer === this.picker.pickedDev.developer)
            return index > 0 ? this.picker.rawData[`${sprint}`][index].totalStoryPoints : 0;
          }
        }), label: 'Story Points' },*/
        { data: this.lineChartLabels.map(sprint => {
          if (this.picker.pickedDev.developer === 'Sprint Summary') {
            return this.picker.getSummary(sprint).totalBugsFixed;
          } else {
            const index = this.picker.rawData[`${sprint}`].findIndex(d => d.developer === this.picker.pickedDev.developer)
            return index > 0 ? this.picker.rawData[`${sprint}`][index].totalBugsFixed : 0;
          }
        }), label: 'Bugs Fixed' },
        { data: this.lineChartLabels.map(sprint => {
          if (this.picker.pickedDev.developer === 'Sprint Summary') {
            return this.picker.getSummary(sprint).totalBugsGenerated;
          } else {
            const index = this.picker.rawData[`${sprint}`].findIndex(d => d.developer === this.picker.pickedDev.developer)
            return index > 0 ? this.picker.rawData[`${sprint}`][index].totalBugsGenerated : 0;
          }
        }), label: 'Bugs Generated' }
      ];
      this.lineChartData[this.lineChartData.length] = { data: (this.lineChartData[this.lineChartData.length - 1].data as any).reduce((acc, act, index) => {
        acc.push((act + ([...this.lineChartData[this.lineChartData.length - 1].data] as any).splice(0,index).reduce(( a, b ) => a + b, 0) ) / (index + 1))
        return acc;
      }, []), label: 'Bug Ratio' };
      console.log(this.lineChartData)
    });
    this.picker.updateData$.next();
  }
  
}
